@media (min-width: 900px) {
    .toggle-side {
        display: none !important;
    }
}
@media (max-width: 900px) {
    .toggle-disp {
        display: none !important;
    }
}
@media (max-width: 1000px) {
    .solo {
        flex-direction: column;
    }
    .center {
        width: 60%;
    }
    .toggle-disp-1000 {
        display: none !important;
    }
}

@media (max-width: 600px) {
    .center {
        width: 100%;
    }
}
